import React, { useState } from 'react';
import { FaCheckCircle as Check, FaTrashAlt as Trash, FaTrash } from 'react-icons/fa';

const Todo = ({ index, todo, handleCompleted, handleDelete, redColor }) => {

    // Handle Style
    const Style = { color: "#f00" }

    return (
        <div
            className="todo-item"
            style={{
                textDecoration: todo.completed ? 'line-through' : '',
            }}
        >
            {todo.text}
            <div className="buttons">
                <Check
                    className="btn"
                    onClick={handleCompleted}
                    style={{ color: todo.completed ? '#080' : '#000' }}
                />
                <Trash
                    className="btn trash"
                    onMouseOver={redColor}
                    onClick={handleDelete}
                    style={{ color: "#000" }}
                />
            </div>
        </div>
    )
}

export default Todo;