import React from 'react';
import { Link } from 'react-router-dom';

const Header = () => {
    return (
        <header>
            <h1>TodoList</h1>
            <div className="links">
                <Link to="/" className="link">Home</Link>
                <span> | </span>
                <Link to="/about" className="link">About</Link>
            </div>
        </header>
    )
}

export default Header;